:host {
  display: block;
}

.header {
  position: sticky;
  top: 0;
  z-index: 1000;
  background-color: var(--white);
  box-shadow: var(--shadow-sm);
}

.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-sm) var(--spacing-md);
  gap: var(--spacing-md);
}

.nav-brand .site-logo {
  max-width: 220px;
  width: 100%;
  height: auto;
  display: block;
}

.nav-menu {
  display: flex;
  gap: var(--spacing-lg);
  align-items: center;
}

.nav-link {
  color: var(--gray-dark);
  text-decoration: none;
  font-weight: 500;
  padding: var(--spacing-xs) 0;
  border-bottom: 2px solid transparent;
  transition: all var(--transition-fast);
}

.nav-link:hover,
.nav-link.active {
  color: var(--verde-escuro);
  border-bottom-color: var(--verde-claro);
}

.nav-actions {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
}

.account-actions {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
}

.account-actions .btn-icon {
  background: var(--off-white);
  border: 1px solid transparent;
}

.account-actions .btn-icon:hover,
.account-actions .btn-icon:focus-visible {
  border-color: var(--verde-escuro);
}

.account-actions .btn-logout {
  background: transparent;
  color: var(--gray-medium);
}

.account-actions .btn-logout:hover {
  color: var(--coral);
}

@media (max-width: 820px) {
  .header .container {
    flex-direction: column;
    align-items: stretch;
  }

  .nav-menu {
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    gap: var(--spacing-md);
  }

  .nav-actions {
    justify-content: center;
  }
}

@media (max-width: 720px) {
  .nav-brand .site-logo {
    max-width: 160px;
    margin: 0 auto;
  }
}
